test("pipe", {
  "given one function"() {
    const inc = (x) => x + 1;
    expect(pipe(inc)(1), is, 2);
  },
  "given two functions"() {
    const inc = (x) => x + 1;
    expect(pipe(inc, inc)(1), is, 3);
  }
});
function rcompose(f, g) {
  return (...args) => g(f(...args));
}
function map(f) {
  return (array) => array.map(f);
}
